* {
    padding: 0;
    margin: 0;
}

.cont1 {
    /* background-color: grey;
    width: 55%;
    padding: 20px;
    border-radius: 1rem; */

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    /* width: 70%; */
    width: 60%;
    background-color: white;
    border-radius: 1rem;
    /* margin-top: 1rem; */
    /* padding: 5rem; */
    padding: 1rem 1.3rem;
    box-shadow: 0 3rem 5rem rgba(0, 0, 0, 0.3);
    z-index: 10;
    
}

.cursor {
    cursor: text !important;
}

.searches {
    display: flex;
    /* align-items: center; */
    justify-content: center;
    height: 25rem;
}

.pera {
    /* margin: 0; */
    padding-top: 3rem;
    color: #99a1b3;
}

.foot_flex {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}

.footer-gap {
    display: flex;
    gap: 5px;
    align-items: center;
}

.footer-gap p {
    margin: 0;
}

button.footer-btn {
    border: none;
    border-radius: 0.2rem;
}

button.footer-btn {
    border: none;
    background: #d8dde5;
    background: linear-gradient(145deg, #dadfe7 0%, #f1f2f4 100%);
    border-radius: 0.1rem;
    height: -moz-fit-content;
    height: fit-content;
    color: #99a1b3;
    font-weight: bold;
    font-size: .7rem;
    padding: 0 4px;
}

.algolia-pera {
    margin: 0;
    font-size: .8rem;
}

.algolia {
    display: flex;
    flex-flow: column;
    gap: 0.8rem;
}

.algolia-pera svg {
    height: 22px;
    width: 20px;
    fill: #5468ff;
    margin: 0 4px;
}

.algolia-pera a {
    text-decoration: none;
    color: #99a1b3;
    font-weight: 500;
}

.algolia-pera a span {
    color: #5468ff;
    /* font-size: 1.2rem;
    font-weight: bold; */
}