* {
    padding: 0;
    margin: 0;
}

li::marker {
    content: none;
}

.App-logo {
    height: 2.5rem;
    pointer-events: none;
    height: 4rem;
    width: 4.5rem;
}

.navs {
    padding: 0;
    align-items: center;
    gap: .75rem;
    margin: 0;
    height: 4rem;
}

.nav-routes {
    display: flex;
    gap: .75rem;
}

.nav-routes li {
    display: flex;
    justify-content: center;
}

.nav-routes-icon {
    display: flex;
    gap: .75rem;
}

.nav-routes li a {
    color: black;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

input.input_w100 {
    border-radius: 2rem;
    /* border-color: #087ea4; */
    background-color: #ebecf0;
    border: none;
    height: 2.5rem;
    cursor: pointer;
    padding-left: 2rem;
    color: #99a1b3;
}

.input_w100 {
    border-color: #087ea4;
}

input.input_w100::placeholder {
    color: #99a1b3;
}

.col-1 {
    padding: 0;
    display: flex;
    justify-content: center;
    max-width: 3.2rem;
}

.col-6 {
    padding: 0;
    width: 55%;
}

.col {
    padding: 0;
}

.col_7 a svg {
    height: 1.2em;
    width: 1.2em;
}

.col_8 a svg {
    height: 1.2em;
    width: 1.2em;
}

.search-input {
    position: relative;
}

.search-input svg {
    position: absolute;
    transform: translateY(-50%);
    top: 50%;
    left: 10px;
    color: #99a1b3;
}

.icon-btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 13px;
}

.flex {
    display: flex;
    gap: 0.3rem;
}

button.ctrl {
    border: none;
    background-color: white;
    padding: 0 8px;
    border-radius: 0.3rem;
    height: fit-content;
    letter-spacing: 1.5px;
    color: #99a1b3;
    font-size: .8rem;
}

.k {
    border: none;
    background-color: white;
    padding: 0 6px;
    border-radius: 0.3rem;
    height: fit-content;
    color: #99a1b3;
    font-size: .8rem;
}

